*{
  margin: 0%;
  padding: 0%;
  overflow-y: hidden;
}
#chatbot-box iframe {
  height: 94vh;
}

#chatbot-box-mobile{
  display: none;
}
.row {
   --bs-gutter-x: 0%;
}
.nav-link{
  color: white;
}

.heading-menu{
  background-color:#2b313f;
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: white;
  height: 95vh;
}
@media screen and (max-width: 700px) {
  #chatbot-box{
      display: none;
  }

div .hello-logo{
  height: 20vh;
}

  #chatbot-box-mobile div .hello-logo{
    background-image:url("./hello.gif");
    background-size: 30%;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    
  }

  #chatbot-box-mobile {
      display: block;    
  }
  
  #chatbot-box-mobile button{
    color: black;
  }
  #chatbot-box-mobile button:hover,#chatbot-box-mobile button:active{
    color: white;
  }
  #chatbot-box-mobile a{
    position: absolute;
    right: 0;
    top: 3%;
    background-color: #2b313f;
  }
  #chatbot-box-mobile iframe{
    height: 90vh;
  }
}